const NewLink = ({ eventKey, cssClass, elseValue, label, stepsCompleted }) => {
    return <Nav.Item className='mx-lg-3'>
        <Nav.Link eventKey={eventKey} className={`w-100 f-center fw-medium`}>
            <div className={`h-24px w-24px rounded-circle me-2 f-center text-gray-100 fs-8 ${cssClass}`}>
                {stepsCompleted ?
                    <FontAwesomeIcon icon={'check'} />
                    : elseValue}
            </div>
            {label}
        </Nav.Link>
    </Nav.Item>
}

