class ShopifyOnboarding extends Component {
    state = { addclass: true, contentObject: null, activeTab: 's_g_t', feedCreated: false, selectedNetworkId: null, stepsCompleted: [], showVideo: false, currentStep: 1, };
    componentDidMount() {
        const { getNetworks } = this.props
    }

    updateData = (obj) => {
        const { updateUserInfo } = this.props
        if (!(onBoardingSteps())) {
            //updateUserInfo(() => { if (obj.SKIP) this.props.navigate('/home') })
            // UPDATE_EXTRA_DATA(obj).then(res => {
            //     updateUserInfo(() => { if (obj.SKIP) this.props.navigate('/home') })
            // })
        }
    }

    onSkipSteps = () => {
        const { updateUserInfo } = this.props
        SkipOnBoarding({ stepstype: "onboardingskip" }).then(res => {
            updateUserInfo(() => { this.props.navigate('/home') })
        }
        )
    }

    onNetworkSelect = (id) => {
        this.setState({ contentObject: feedFilters[id], activeTab: "a_c", selectedNetworkId: id, stepsCompleted: [...this.state.stepsCompleted, "chooseNetwork"] }, () => this.updateData({ CHOOSE_NETWORK: id }))
    }

    onClose = event => this.setState({ showVideo: !this.state.showVideo })

    handleStep = (val) => {
        this.setState({ activeTab: val, contentObject: [], stepsCompleted: [] })
    }
    galleryFun = () => {
        this.setState({activeTab:'c_n'})
    }

    render() {
        const { contentObject, selectedNetworkId, activeTab, stepsCompleted, showVideo } = this.state
        return <div className='d-flex flex-column flex-column-fluid align-items-center justify-content-center w-100 flex-position-center full-content'>
            <Container className='onboard__' fluid>
                <Tab.Container defaultActiveKey="s_g_t" activeKey={activeTab}>
                    <Card>
                        <Card.Header className='pb-0 w-100 align-items-center'>
                            <div></div>
                            <Nav variant="underline" className="nav-lg flex-sm-row flex-column ms-sm-10">
                                <NewLink eventKey="s_g_t" cssClass={`bg-primary fw-semibold ${activeTab=='c_n' || activeTab=='a_c' ? 'bg-primary' : 'bg-dark'}`} elseValue="1" label="Select Gallery Type" stepsCompleted={(activeTab=='c_n' || activeTab=='a_c')} />
                                <NewLink eventKey="c_n" cssClass={activeTab=='a_c' ? 'bg-primary' : 'bg-dark'} elseValue="2" label="Choose network" stepsCompleted={activeTab=='a_c'} />
                                <NewLink eventKey="a_c" cssClass={activeTab=='' ? 'bg-primary' : 'bg-dark'} elseValue="3" label="Add Content" stepsCompleted={activeTab==''} />
                            </Nav>
                            <Button size='sm' variant='light' className='d-none d-sm-inline-block' onClick={this.onSkipSteps}>Skip</Button>
                        </Card.Header>
                        <Card.Body>
                            <Tab.Content className='onboard_tabs_'>
                                <Tab.Pane eventKey="s_g_t">
                                    {activeTab == 's_g_t' ? <ChooseGallery galleryFun={this.galleryFun} /> : null}
                                </Tab.Pane>
                                <Tab.Pane eventKey="c_n">
                                    {activeTab == 'c_n' ? <ChooseNetwork onNetworkSelect={this.onNetworkSelect} /> : null}
                                </Tab.Pane>
                                <Tab.Pane eventKey="a_c">
                                    {activeTab == 'a_c' ? contentObject ? 
                                    <AddContent contentObject={contentObject} selectedNetworkId={selectedNetworkId} updateData={this.updateData} handleStep={this.handleStep} onNext={() => this.setState({ stepsCompleted: [...stepsCompleted, 'addContent'] },()=> setTimeout(() => { this.props.navigate('/home') }, 1000))} /> 
                                    : null : null}
                                </Tab.Pane>
                            </Tab.Content>
                        </Card.Body>
                        <Card.Footer className='border-0 bg-gray-100'>
                            <Container fluid='sm'>
                                <Row className='justify-content-center'>
                                    <Col lg={8} className='f-center justify-content-between'>
                                        <div>
                                            <Button variant='link' className='d-inline-block d-sm-none py-2' onClick={this.onSkipSteps}>Skip</Button>
                                        </div>
                                        <div className='f-between flex-sm-grow-1'>
                                            <span className='me-5 d-sm-flex d-none'>In case of any query reach out to us at support@tagshop.ai or Talk to expert</span>
                                            <div className='d-flex flex-nowrap'>
                                                <Link className='btn btn-outline-primary text-nowrap' to={HUBSPOT_URL} target='_blank'>
                                                    <FontAwesomeIcon icon={'phone'} iconClass={'me-1'} />
                                                    Demo Call
                                                </Link>
                                                <Button variant='outline-primary text-nowrap' className='ms-3' onClick={() => { this.setState({ showVideo: true }) }}>
                                                    <FontAwesomeIcon icon={'circle-play'} iconClass={'me-1'} />
                                                    Watch Video
                                                </Button>
                                            </div>
                                        </div>
                                    </Col>
                                </Row>
                            </Container>
                        </Card.Footer>
                        <SupportVideo show={showVideo} link={'https://player.vimeo.com/video/912907290?h=f28a39da95'} onClose={this.onClose} />
                    </Card>
                </Tab.Container>
            </Container>
        </div>
    }
}

const mapStateToProps = (state) => {
    return {
        stepData: state.dashboardData?.dashboarddata?.extra_data
    }
}

const mapDispatchToProps = dispatch => {
    return {
        getNetworks: (callback) => dispatch(GET_ADD_FEED(callback)),
        getModeration: () => dispatch(getModeration()),
        updateUserInfo: (callBack) => dispatch(getUserInfo_afterChanges(callBack)),
    }
}

export default connect(mapStateToProps, mapDispatchToProps)(withRouter(ShopifyOnboarding))